<template>
    <div class="my">
        <van-nav-bar title="个人中心">
            <template #right>
                <van-icon name="setting-o" size="18" />
            </template>
        </van-nav-bar>

        <div class="my_content">
             <div class="userImg" :style="{backgroundImage: 'url('+userInfo.userBg+')'}">
                 <div class="userImg_title">
                     <div class="userImg_src" :style="{backgroundImage: 'url('+userInfo.userImg+')'}"></div>
                     <div class="userImg_name" v-if="isLogin">{{userInfo.nickName}}</div>
                      <div class="userImg_name" @click="goLogin" v-else>登录</div>
                 </div>
                 <div class="userLiuYan">{{userInfo.desc==""?'未曾留言....':userInfo.desc}}</div>
                 <div class="userBakg">
                     <div class="user_background" v-if="isLogin">更换背景</div>
                      <van-uploader class="upload-box"  :after-read="afterRead" :max-size="1024 * 1024"/>
                 </div>
             </div>
             <div class="basicFunction">
                <van-cell :title="item.title" is-link  v-for="(item,index) in userTrack" :key="index" @click="userClickSelect(item,index)"/>
             </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                userTrack:[
                    {title: '账号管理',isActives: true,name: 'UserManage'},
                    {title: '地址管理',isActives: false,name: 'MyAddress'},
                    {title: '我的收藏',isActives: false,name: 'Collection'},
                    {title: '安全中心',isActives: false,name: 'ModifyPW'}
                ],
                userInfo:{},
                isLogin: true
                // spanLine: '10px',
                // trans: 'all .5s'
            }
        },
        created(){
           this.getMyInfoData();
           if(localStorage.getItem('__Tk')==null){
               this.isLogin=false;
           }
        },
        methods: {
            afterRead(file){//str.endsWith(".jpg")
                  let tokenString = localStorage.getItem('__Tk');
                 let fileName=file.file.name;
                 let isPhtoto = str => {
                    if(fileName.endsWith(".jpg") || fileName.endsWith(".png") || fileName.endsWith(".gif") || fileName.endsWith(".jpeg")){
                        return true;
                    }else {
                        return false;
                    }
                }
                
                
                let baseImg=file.content.split('64,')[1];
                
                if(isPhtoto){
                    this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                    })

                    this.axios({
                        method: "POST",
                        url: '/updateUserBg',
                        data:{
                            appkey: this.appkey,
                            tokenString,
                            imgType: fileName.split('.')[1],
                            serverBase64Img: baseImg
                        }
                    }).then(result=>{
                        this.$toast.clear();
                        this.userInfo.userBg=result.data.userBg;
                        
                    }).catch(err=>{
                        this.$toast.clear();
                        
                    })
                }
                
            },
            getMyInfoData(){
                let tokenString = localStorage.getItem('__Tk');

                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })

                this.axios({
                    method: "GET",
                    url: '/findMy',
                    params:{
                       appkey: this.appkey,
                       tokenString,
                    }
                }).then(result=>{
                    this.$toast.clear();
                    if(result.data.code=="A001"){
                        this.userInfo=result.data.result[0];
                    }
                    
                }).catch(err=>{
                    this.$toast.clear();
                    
                })
            },
            userClickSelect(item,index){
                if(!this.isLogin){
                  this.$toast.fail('请先登录');
                  return;
                }
                   this.$router.push({name: item.name});
            },
            goLogin(){
                this.$router.push({name: 'Login'})
            }
            
        }
        
    }
</script>

<style lang="less" scoped>
   .my{
       position: fixed;
       top:0px;
       left:0px;
       right:0px;
       bottom: 50px;
   }
   .my_content{
       height: calc(~"100% - 46px");
       width: 100%;
       overflow-y: auto;

       .userImg{
           height: 150px;
           width: 100%;
           background-color: #666;
           background-size: cover;
           position: relative;
           color: #f0f0f0;
       }
       .userImg_title{
           width: 100px;
           height: 100px;
           padding: 20px 0px 0px 0px;
           margin: 0px auto;
        //    background-color: red;
       }
       .userImg_src{
           width: 80px;
           height: 70px;
           border-radius: 50%;
           margin: 0px auto;
           background-color: pink;
           background-size: cover;
       }
       .userImg_name{
           height: 30px;
          width: 100%;
          line-height: 30px;
          font-size: 14px;
          text-align: center;
          
       }
      .userLiuYan{
          height: 30px;
          line-height: 30px;
          width: calc(~"100% - 50px");
          text-align: center;
          color: #fff;
        
      }
      .userBakg{
          position: absolute;
          top: 10px;
          font-size: 13px;
          right: 10px;
          
      }
      .upload-box{
          top: 0;
          left: 0;
          position: absolute;
          z-index: 5;
          width: 100%;
          overflow: hidden;
          height: 17px;
          opacity: 0;
      }
   }
</style>